import { useState, useEffect } from 'react';
import { Platform } from 'react-native';
import { supabase } from '../utils/supabase';
import { GOOGLE_CLIENT_ID } from '../config/env';
import { getEnvironmentConfig, isDevelopment } from '../config/environment';

// Get the Google OAuth redirect URL for web
const getRedirectUrl = () => {
  if (Platform.OS !== 'web') return undefined;

  const config = getEnvironmentConfig();
  // For development, use the localhost:19006 URL
  if (isDevelopment()) {
    return 'http://localhost:19006/auth/callback';
  }
  return `${config.webUrl}/auth/callback`;
};

// Make sure to configure these URLs in Google Cloud Console:
// Development:
// - Origins: http://localhost:19006, http://localhost:3000
// - Redirect: http://localhost:3000/auth/callback
//
// Staging:
// - Origin: https://staging.foilquiver.com
// - Redirect: https://staging.foilquiver.com/auth/callback
//
// Production:
// - Origin: https://foilquiver.com
// - Redirect: https://foilquiver.com/auth/callback

export const useGoogleAuth = () => {
  const [isLoading, setIsLoading] = useState(false);

  // Handle OAuth redirect on mount
  useEffect(() => {
    if (Platform.OS === 'web') {
      // Check if we have an access token in the URL
      const params = new URLSearchParams(window.location.hash.substring(1));
      const accessToken = params.get('access_token');
      
      if (accessToken) {
        console.log('Found access token in URL, handling auth redirect...');
        supabase.auth.getSession().then(({ data: { session }}) => {
          if (!session) {
            console.log('No session found, attempting to set session from URL...');
            // Handle the OAuth redirect
            supabase.auth.setSession({
              access_token: accessToken,
              refresh_token: params.get('refresh_token') || '',
            }).catch(console.error);
          }
        });
      }
    }
  }, []);

  const signInWithGoogle = async () => {
    try {
      setIsLoading(true);
      if (Platform.OS !== 'web') {
        console.warn('Mobile authentication not yet implemented');
        return { data: null, error: new Error('Mobile authentication not yet implemented') };
      }

      const redirectTo = getRedirectUrl();
      console.log('Starting Google OAuth flow with redirect:', redirectTo);
      
      const { data, error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: {
          redirectTo,
          queryParams: {
            access_type: 'offline',
            prompt: 'consent',
          },
          skipBrowserRedirect: false,
        },
      });

      if (error) {
        console.error('Google OAuth error:', error);
      } else {
        console.log('OAuth flow initiated successfully');
      }

      return { data, error };
    } catch (error) {
      console.error('Unexpected error during Google sign in:', error);
      return { data: null, error };
    } finally {
      setIsLoading(false);
    }
  };

  return {
    signInWithGoogle,
    isLoading,
  };
}; 